.mobile-takeover {
    display: flex;
    justify-content: center;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
    position: fixed;
    width: 100%;
    height: calc(100vh);
    background-color: #1C1F21;
    border: 20px solid #92683C;
    z-index: 100;
    color: #fff;
    a {
        color: #fff;
    }
}

.mobile-takeover .f-logo {
    width: 30%;
    margin: 0 auto 2em;
}

.mobile-takeover nav {
    text-align: center;
    width: 100%;
}

.sm-century {
    font-size: .7em;
    text-transform: uppercase;
    letter-spacing: 5px;
}

.mobile-takeover nav .mobile-navigation {
    list-style: none;
}

.mobile-takeover nav .mobile-navigation li {
    display: block;
    padding: 1em;
}

.icon-close, .icon-close:after, .md-close span:after {
    display: block;
}

.icon-close:after, .md-close span:after {
    font-family: 'icomoon';
    speak: none;
    font-style: normal;
    font-weight: normal;
    font-variant: normal;
    text-transform: none;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

.mobile-open__inner {
    display: inline-block;
    position: fixed;
    right: 1.5em;
    top: 1.25em;
    z-index: 1000;
}
