
$color-light: #8c1d4d;
$color-dark: #af2b65;

.content {
  box-shadow: inset 0px 12px 25px 5px rgba(0, 0, 0, 0.4);
  background-image: linear-gradient(45deg, rgba(110,91,91,0.5) 25%, transparent 25%), linear-gradient(-45deg, rgba(110,91,91,0.5) 25%, transparent 25%), linear-gradient(45deg, transparent 75%, rgba(110,91,91,0.5) 75%), linear-gradient(-45deg, transparent 75%, rgba(110,91,91,0.5) 75%);
    background-size: 200px 200px;
    background-position: 0 0, 0 100px, 100px -100px, -100px 0px;
  text-align: center;
  padding: 4em 0;
  background-color: #edebe1;
}

.cards {
  display: flex;
  justify-content: space-evenly;
}

.card {
  width: 25%;
  max-width: 280px;
  position: relative;
  height: 25em;
  perspective: 150em;
   transition: all .2s cubic-bezier(0.000, 0.000, 0.580, 1.000); 
  &_side {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 25em;
    backface-visibility: hidden;
    transition: all .6s ease;
    box-shadow: 1em 1em 2em rgba(0,0,0,.2);
    overflow: hidden;
    box-sizing: border-box;
   border-radius: 10px;
    &-front {
      background-color: #FFF; 
      transform: rotateY(180deg);
    }
    
    &-back {
      color: #FFF;
      border: solid 1px #b29893;
      background-image: linear-gradient(to right bottom, $color-light, $color-dark);          padding: 40px;
    }
  }
  &:hover {
    cursor: pointer;
      transform: scale(1.1);
  }
  
  &:hover &_side-back {
    transform: rotateY(-180deg);
    
  }
  
  &:hover &_side-front {
    transform: rotate(0);
  }
  
 
}

 .card_description {
    text-transform: uppercase;
    font-size: 5em;
    padding: 1em 0;
    box-sizing: border-box;
}
.card_side-back .card_description {
    border: solid #845d1f 1px;
  height: 100%;
}
.card_side-front .card_description {
    position: relative;
  height: 100%;
}
 .card_gara{
   width: 200%;
    height: 143%;
    position: absolute;
    left: -186px;
    top: 0;
  height: 100%;
transform: rotate(45deg);
  background-image:
  repeating-linear-gradient(
    90deg,
    #93212a ,
    #93212a 1px,
    transparent 1px,
    transparent 32px
  ),
  repeating-linear-gradient(
    0deg,
    #93212a ,
    #93212a 1px,
    rgba(0,0,0,0) 1px,
    rgba(0,0,0,0) 32px
  );


  }

.card_num {
  color: #000;
  position: absolute;
  font-size: 0.4em;
  &.num-left {
    left: 4%;
    top: 0;
  }
  &.num-right {
    right: 4%;
    bottom: 0;
    transform: rotate(
180deg);
  }
}


.icon-heart {
      width: 50px;
      height: auto;
      fill: #891010;
}

.card_icon {
  position: absolute;
  top: 4%;
  left: 0;
  right: 0;
  margin: 0 auto;
  display: flex;
  flex-wrap: wrap;
  width: 90%;
  span {
    display: block;

  }
  span:nth-child(1),span:nth-child(2),
  span:nth-child(4),span:nth-child(5),
  span:nth-child(6),span:nth-child(7),
  span:nth-child(9),span:nth-child(10){
    width: 50%;  
    height: 48px;
  }
  span:nth-child(3), span:nth-child(8) {
    width: 100%;
    height: 42px;
  }
}



