body {
  overflow: hidden;
}
.on-scroll {
  overflow: inherit;
}

.curtain {
  position: fixed;
  width: 100%;
  height: 100vh;
  display: flex;
  left: 0;
  top: 0;
  z-index: 20;
  img,svg {
    display: block;
    height: 100vh;
  }
}

.curtain-left,.curtain-right{
  transform: scale(1, 1.2);
  background-color: #b71c1f;
    width: 50%;
  height: 100vh;
      border: 1px solid #990000;
      box-sizing: border-box;
  transition: 2.4s;
}

.curtain-left {
  transform-origin:left top;
}
.curtain-right{
  transform-origin:right top;
}

.is-open .curtain-left,.is-open .curtain-right{
  transform: scale(0.08, 1.2);
}
.curtain {
  transition: 1s;
  &.is-hide {
    opacity: 0;
    z-index: -1;

  }
}



body {
