.topbar {
  padding: 1em 0;
    border-bottom: 1px solid #3ba99b;
  background: #88c5c6;
  z-index: 20;
  position: fixed;
  top: 0;
  -webkit-transition: top 0.2s ease-in-out;
  transition: top 0.2s ease-in-out;
  width: 100%;
}

.topbar__navigation {
  text-align: center;
  list-style: none;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  -ms-flex-wrap: nowrap;
  flex-wrap: nowrap;
}
@media screen and (max-width: 800px) {
  .topbar__navigation {
      display: none;
  }
}

.show-nav #body-nav {
  -webkit-transform: scale(1);
  transform: scale(1);
}

#body-nav {
  -webkit-transition: all 300ms ease 0ms;
  transition: all 300ms ease 0ms;
  -webkit-transform: scale(0);
  transform: scale(0);
  z-index: 100;
  top: 0;
}



.md-overlay {
  position: fixed;
  width: 100%;
  height: 100%;
  visibility: hidden;
  top: 0;
  left: 0;
  z-index: 1000;
  opacity: 0;
  background: rgba(28, 31, 33, 0.9);
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}

.md-modal {
  position: fixed;
  top: 50%;
  left: 50%;
  width: 60%;
  max-width: 700px;
  min-width: 300px;
  height: auto;
  z-index: 2000;
  visibility: hidden;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  -webkit-transform: translateX(-50%) translateY(-50%);
  transform: translateX(-50%) translateY(-50%);
}

.topbar__table {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
  justify-content: space-around;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  width: 100%;
  height: 45px;
  position: relative;
}

.topbar__navigation li a {
  font-size: .85em;
  letter-spacing: 2px;
