  img {
    width: 100%;
    height: auto;
  }
  .face {
    position: absolute;
    z-index: 4;
    top: 0;
    transform-origin: bottom center;
    animation: burabura linear 5s infinite;
  }
  .shoulder {
    width: 70%;
    position: absolute;
    top: 220px;
    left: 0;
    right: 0;
    margin: 0 auto;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    z-index: 5;
  }
  .ribbon {
    position: absolute;
    width: 50%;
    top: 210px;
    left: 0;
    right: 0;
    margin: 0 auto;
    z-index: 6;
  }
  .shoulder {

    .left {
      width: 26%;
    }
    .right {
      width: 26%;
    }
  }
  .body {
    width: 80%;
    margin: 0 auto;
    z-index: 3;
    position: absolute;
    top: 210px;
    left: 0;
    right: 0;
  }
  .foot {
    width: 120px;
    position: absolute;
    top: 340px;
    left: 0;
    right: 0;
    margin: 0 auto;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    z-index: 2;
    div {
      width: 50%;
    }
    .left {
      transform-origin: top right;
      animation: burabura linear 8s infinite;
    }
    .right {
