  0% {
    right: -80px;
  }
  10% {
    right: 4vw;
    top: 4%;
  }
  40% {
    right: 40vw;
    top: 30%;
  }
  60% {
    right: 58vw;
    top: 4%;
  }

  100% {
      right: 120vw;
      top: 50%;
  }
}


