.content
{
    padding: 4em 0;

    text-align: center;

    background-color: #edebe1;
    background-image: linear-gradient(45deg, rgba(110, 91, 91, .5) 25%, transparent 25%), linear-gradient(-45deg, rgba(110, 91, 91, .5) 25%, transparent 25%), linear-gradient(45deg, transparent 75%, rgba(110, 91, 91, .5) 75%), linear-gradient(-45deg, transparent 75%, rgba(110, 91, 91, .5) 75%);
    background-position: 0 0, 0 100px, 100px -100px, -100px 0;
    background-size: 200px 200px;
    box-shadow: inset 0 12px 25px 5px rgba(0, 0, 0, .4);
}

.cards
{
    display: flex;

    justify-content: space-evenly;
}

.card
{
    position: relative;

    width: 25%;
    max-width: 280px;
    height: 25em;

    transition: all .2s cubic-bezier(0, 0, .58, 1);

    perspective: 150em;
}

.card_side
{
    position: absolute;
    top: 0;
    left: 0;

    overflow: hidden;

    box-sizing: border-box;
    width: 100%;
    height: 25em;

    transition: all .6s ease;

    border-radius: 10px;
    box-shadow: 1em 1em 2em rgba(0, 0, 0, .2);

    backface-visibility: hidden;
}

.card_side-front
{
    transform: rotateY(180deg);

    background-color: #fff;
}

.card_side-back
{
    padding: 40px;

    color: #fff;
    border: solid 1px #b29893;
    background-image: linear-gradient(to right bottom, #8c1d4d, #af2b65);
}

.card:hover
{
    cursor: pointer;
    transform: scale(1.1);
}

.card:hover .card_side-back
{
    transform: rotateY(-180deg);
}

.card:hover .card_side-front
{
    transform: rotate(0);
}

.card_description
{
    font-size: 5em;

    box-sizing: border-box;
    padding: 1em 0;

    text-transform: uppercase;
}

.card_side-back .card_description
{
    height: 100%;

    border: solid #845d1f 1px;
}

.card_side-front .card_description
{
    position: relative;

    height: 100%;
}

.card_gara
{
    position: absolute;
    top: 0;
    left: -186px;

    width: 200%;
    height: 143%;
    height: 100%;

    transform: rotate(45deg);

    background-image: repeating-linear-gradient(90deg, #93212a, #93212a 1px, transparent 1px, transparent 32px), repeating-linear-gradient(0deg, #93212a, #93212a 1px, rgba(0, 0, 0, 0) 1px, rgba(0, 0, 0, 0) 32px);
}

.card_num
{
    font-size: .4em;

    position: absolute;

    color: #000;
}

.card_num.num-left
{
    top: 0;
    left: 4%;
}

.card_num.num-right
{
    right: 4%;
    bottom: 0;

    transform: rotate(180deg);
}

.icon-heart
{
    width: 50px;
    height: auto;

    fill: #891010;
}

.card_icon
{
    position: absolute;
    top: 4%;
    right: 0;
    left: 0;

    display: flex;

    width: 90%;
    margin: 0 auto;

    flex-wrap: wrap;
}

.card_icon span
{
    display: block;
}

.card_icon span:nth-child(1),
.card_icon span:nth-child(2),
.card_icon span:nth-child(4),
.card_icon span:nth-child(5),
.card_icon span:nth-child(6),
.card_icon span:nth-child(7),
.card_icon span:nth-child(9),
.card_icon span:nth-child(10)
{
    width: 50%;
    height: 48px;
}

.card_icon span:nth-child(3),
.card_icon span:nth-child(8)
{
    width: 100%;
    height: 42px;
}

