@keyframes btf
{
    0%
    {
        left: -20%;

        transform: rotate(20deg);
    }
    60%
    {
        left: 60%;
    }
    70%
    {
        left: 48%;

        transform: rotate(18deg);
    }
    80%
    {
        left: 54%;

        transform: rotate(-10deg);
    }
    90%
    {
        left: 52%;
    }
    100%
    {
        left: 60%;

        transform: rotate(1deg);
    }
}

@keyframes btfh
{
    0%
    {
        top: 0;
    }
    100%
    {
        top: 30px;
    }
}

