/* Loading Block */
#loading
{
    position: fixed;
    z-index: 9999;
    top: 0;
    left: 0;

    width: 100vw;
    height: 100vh;

    transition: all 1s;

    background-color: rgba(18, 132, 129, .8);
}

.spinner
{
    width: 100px;
    height: 100px;
    margin: 200px auto;

    animation: sk-scaleout 1.0s infinite ease-in-out;

    border-radius: 100%;
    background-color: #fff;
}

/* Loading Animation */
@keyframes sk-scaleout
{
    0%
    {
        transform: scale(0);
    }
    100%
    {
        transform: scale(1);

        opacity: 0;
    }
}

/* Hide Loading Block */
.loaded
{
    visibility: hidden;

    opacity: 0;
}

