#second
{
    position: relative;

    width: 100%;

    background: url(../../img/doll-bg.jpg);
    background-size: 100% 100%;
}

#second p
{
    opacity: 0;
}

#second .doll-left
{
    position: absolute;
    right: -80px;

    margin-top: 10%;

    transition: transform 8s;

    opacity: .6;

    filter: drop-shadow(5px 5px 0 rgba(28, 31, 33, .1));
}

#second.active .doll-left
{
    animation-name: slide;
    animation-duration: 12s;
    animation-timing-function: ease;

    opacity: 1;
}

@keyframes slide
{
    0%
    {
        right: -80px;
    }
    10%
    {
        top: 4%;
        right: 4vw;
    }
    40%
    {
        top: 30%;
        right: 40vw;
    }
    60%
    {
        top: 4%;
        right: 58vw;
    }
    100%
    {
        top: 50%;
        right: 120vw;
    }
}

@keyframes textFadeIn
{
    0%
    {
        transform: rotate(0) scale(.2);

        opacity: 0;
    }
    50%
    {
        transform: rotate(-45deg) scale(.6);

        opacity: .5;
    }
    100%
    {
        transform: rotate(0) scale(1);

        opacity: 1;
    }
}

#second2.active
{
    background: #000;
}

.doll-left
{
    position: relative;

    width: 220px;
    margin: 0 auto;
}

.doll-left img
{
    width: 100%;
    height: auto;
}

.doll-left .face
{
    position: absolute;
    z-index: 4;
    top: 0;
    left: 20px;

    width: 82%;

    transform-origin: bottom center;
    animation: burabura linear 8s infinite;
}

.doll-left .shoulder
{
    position: absolute;
    z-index: 5;
    top: 220px;
    right: 0;
    right: 0;

    width: 70%;
}

.doll-left .ribbon
{
    position: absolute;
    z-index: 6;
    top: 210px;
    right: 0;
    left: -40px;

    width: 20%;
    margin: 0 auto;
}

.doll-left .hand
{
    width: 58%;

    transform-origin: top right;
    animation: burabura linear 3s infinite;
}

.doll-left .body
{
    position: absolute;
    z-index: 3;
    top: 210px;
    right: 0;
    left: 0;

    width: 80%;
    margin: 0 auto;
}

.doll-left .foot
{
    position: absolute;
    z-index: 2;
    top: 340px;
    right: 0;
    left: 0;

    display: flex;

    width: 120px;
    margin: 0 auto;

    flex-wrap: wrap;
    justify-content: space-between;
}

.doll-left .foot div
{
    width: 50%;

    transform-origin: top left;
}

.doll-left .foot .left
{
    animation: burabura linear 1.8s infinite;
}

.doll-left .foot .right
{
    animation: burabura linear 1.8s infinite -1s;
}

.doll-left .key
{
    position: absolute;
    top: 240px;
    right: -40px;

    display: flex;

    width: 100px;

    flex-wrap: wrap;
}

.doll-left .key_set
{
    width: 20%;
}

.doll-left .key_rote
{
    width: 80%;

    animation: r6 2s linear infinite;
}

@keyframes burabura
{
    0%
    {
        transform: rotate(0deg);
    }
    25%
    {
        transform: rotate(-10deg);
    }
    50%
    {
        transform: rotate(0deg);
    }
    75%
    {
        transform: rotate(10deg);
    }
    100%
    {
        transform: rotate(0deg);
    }
}

@keyframes r6
{
    0%
    {
        transform: rotateX(0deg);
    }
    100%
    {
        transform: rotateX(360deg);
    }
}
